<template>
	<div id="member-phonic">
		<public-header title="语音设置"></public-header>
		<div class="content">
			<div class="padding-10-tb padding-15-lr font-14 c-gray">语音提示</div>
			<van-cell-group>
				<van-cell title="微信模板消息提醒">
					<iswitch :value="deliveryer.extra.accept_wechat_notice" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="deliveryer.extra.accept_wechat_notice" :extra="{which: 'accept_wechat_notice'}"></iswitch>
				</van-cell>
				<van-cell title="语音电话提醒">
					<iswitch :value="deliveryer.extra.accept_voice_notice" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="deliveryer.extra.accept_voice_notice" :extra="{which: 'accept_voice_notice'}"></iswitch>
				</van-cell>
				<van-cell title="语音提示">
					<iswitch :value="phonic.voice" text-open="ON" text-close="OFF" @change="jsTogglePhonic" keys="phonic.voice"></iswitch>
				</van-cell>
				<van-cell title="新消息振动">
					<iswitch :value="phonic.vibrate" text-open="ON" text-close="OFF" @change="jsTogglePhonic" keys="phonic.vibrate"></iswitch>
				</van-cell>
			</van-cell-group>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">铃声</div>
			<van-radio-group v-model="phonic.voice_type" @change="onChangeVoiceType">
				<van-cell-group>
					<van-cell title="音乐" clickable @click="phonic.voice_type = 2">
						<van-radio :name="2" />
					</van-cell>
					<van-cell title="订单内容" clickable @click="phonic.voice_type = 1">
						<van-radio :name="1" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData} from "@/controller/funcCommon"
	import cloudApp from "@/config/cloudapp.js"
	export default {
		components: {
			publicHeader,
			iswitch
		},
		data() {
			return {
				deliveryer: {
					extra: {
						accept_voice_notice: 0,
						accept_wechat_notice: 0,
					}
				},
				phonic: {
					voice: 1,
					vibrate: 1,
					voice_type: 1
				},
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/member/mine/index',
					autoAssign: true,
					variable: 'deliveryer',
					success: function(res){
						that.deliveryer = that.util.extend(that.deliveryer, res.deliveryer);
						let phonic = cloudApp.igetPrefs('idphonic');
						that.phonic = Object.assign(that.phonic, phonic);
					}
				});
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value,
					url: 'delivery/member/mine/setting',
					data: {
						which: res.which,
					}
				});
			},
			jsTogglePhonic(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value,
					success: () => {
						cloudApp.isetPrefs('idphonic', this.phonic);
					}
				});
			},
			onChangeVoiceType(name) {
				var that = this;
				cloudApp.isetPrefs('idphonic', that.phonic);
			},

		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#member-phonic .iswitch{
		height: 24px;
	}
</style>


